methods: {

getBannerImg () {
let _this = this
this.$axios.get('/api/homePage/getBanner').then(function (data) {
if (data.data.code === '200') {
let arrData = data.data.data
if (!data.data.data) {
return false
}
let imgArr = []
for (let i = 0; i < arrData.length; i++) {
imgArr.push({
url: '/nopay/details?sellerTaskId=' + arrData[i].sellerTaskId,
img: arrData[i].picUrl,
sellerTaskId: arrData[i].sellerTaskId
})
}
_this.scrollImgs = imgArr
} else {
_this.$vux.alert.show({
title: '',
content: '',
onShow () {
console.log('Plugin: I\'m showing')
},
onHide () {
console.log('Plugin: I\'m hiding')
}
})
}
}).catch(function (err) {
console.log(err)
_this.$vux.alert.show({
title: '',
content: ',',
onShow () {
console.log('Plugin: I\'m showing')
},
onHide () {
console.log('Plugin: I\'m hiding')
}
})
})
},

getRecommendLottery () {
this.$axios.get('/api/homePage/getRecommendLottery').then((data) => {
if (data.data.code === '200') {
let arrList = []
if (!data.data.data) {
return false
}
for (let m of data.data.data) {
let goods = {
imgSrc: m.picUrl,
hot: m.isRecommend,
vip: m.isPlus,
info: m.productName,
progress: m.percent,
sellerTaskId: m.sellerTaskId,
msg: {
pric: m.price,
state: m.isMore,
zhuan: m.extraGold,
baina: ''
}
}
arrList.push(goods)
}
this.arrImg1 = arrList
this.$nextTick(() => {
this.$refs.scroll.refresh()
})
}
}).catch(function (err) {
console.log(err)
})
},

